    perspective: 900px;
    .auto-cover-flow-content {
        transform: rotateY(-8deg) rotateZ(2deg);
        .cover-icon {
            position: absolute;
            width: 405px;
            height: 426px;
            box-shadow: 8px 20px 50px rgba(26, 32, 60, 0.4);
            border-radius: 15px;
            /*animation: testCir2 24s linear infinite normal;*/
        }
        .icon1 {
            right: 100px;
