            opacity: 0.8;
            transform: scale(0.82);
            //background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/three-unlock-1.png') center no-repeat;
        }
        .icon2 {
            right: -62px;
            z-index: 1;
            opacity: 0.8;
            transform: scale(0.82);
            //background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/three-unlock-2.png') center no-repeat;
        }
        .icon3 {
            right: 19px;
            z-index: 10;
            opacity: 1;
            transform: scale(1);
            //background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/three-unlock-3.png') center no-repeat;
        }
    }
}

.auto-cover-flow-container-mb {
    perspective: 900px;
