                    nextEl: '.swiper-button-next',
                }
            },
            isPc: this.isPc()
        };
    },
    props: {
        isChangeLang: {}
    },
    watch: {
        isChangeLang: {
            immediate: true,
            handler(val) {
                if (val.value) {
                    this.showReview = false;
                    this.$nextTick(() => {
                        this.showReview = true
                    })
                }
            }
        }
    },
    created() {
        if (!this.isPc) {
            this.swiperOption = this.swiperOptionMB
        }
    },
    methods: {
        clickMoveCard(isAdd) {
            let mySwiper = this.$refs.mySwiper.$swiper;
            if ((!isAdd && mySwiper.activeIndex > 2) || (isAdd && mySwiper.activeIndex < 8)) {
                mySwiper.slideTo(isAdd ? mySwiper.activeIndex + 1 : mySwiper.activeIndex - 1, 1000,false);
                mySwiper.autoplay.start();
            }
        }
    }
};
