<template>
    <div :class="{'speed-test-container': isPc, 'speed-test-container-mb': !isPc}" id="speed-test-el"
    >
        <div class="speed-test-bg" v-if="isPc"></div>
        <div class="speed-test-content">
            <canvas id="canvas-bg" width="296" height="296"></canvas>
            <canvas id="canvas-down" width="296" height="296"></canvas>
            <!--<canvas id="canvas-up" width="276" height="276"></canvas>-->
            <div class="test-down-arr" id="test-down-arr" :style="{'transform': arrAngle}">
                <!--<i :class="{'cyan-arr': !isUpLoad, 'violet-arr': isUpLoad}"></i>-->
                <i :class="{'i-upload': isUpLoad, 'i-download': !isUpLoad}"></i>
            </div>
            <div class="speed-number">
                <span class="speed-0">0</span>
                <span class="speed-1">5</span>
                <span class="speed-2">10</span>
                <span class="speed-3">15</span>
                <span class="speed-4">25</span>
                <span class="speed-5">50</span>
                <span class="speed-6">75</span>
                <span class="speed-7">150</span>
                <span class="speed-8">300</span>
            </div>
            <div class="speed-data-container">
                <p class="speed-live">{{liveRatBuf.toFixed(2)}}</p>
                <p class="speed-unit">Mbps</p>
                <span class="test-btn" id="speed-test-btn" :class="{'test-btn-disable': (testProgress !== 0 && testProgress !== 3)}"
                      @click="userClickCall" >GO</span>
                <div class="speed-data-list">
                    <div class="speed-data">
                        <!--<p v-if="downLoadRat > 0" class="speed-test test-down">{{downLoadRat.toFixed(2)}}</p>-->
                        <!--<div v-if="downLoadRat <= 0" class="init-line test-down"></div>-->
                        <div class="speed-rat-content">
                            <i :class="{'down-init': testProgress <= 1, 'down-done': testProgress > 1}"></i>
                            <p class="speed-test" v-if="testProgress > 1">{{downLoadRat.toFixed(2)}}</p>
                            <i class="rat-init" v-if="testProgress <= 1"></i>
                        </div>
                        <p class="unit-test">Mbps</p>
                    </div>
                    <div class="speed-data speed-data-up">
                        <div class="speed-rat-content">
                            <i :class="{'up-init': testProgress !== 3, 'up-done': testProgress === 3}"></i>
                            <p class="speed-test" v-if="testProgress === 3">{{upLoadRat.toFixed(2)}}</p>
                            <i class="rat-init" v-if="testProgress !== 3"></i>
                        </div>
                        <p class="unit-test">Mbps</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {TimelineMax} from 'gsap';
export default {
    name: 'SpeedTestView',
    data() {
        return {
            speedRat: 0,
            arrAngle: 'rotate(-136deg)',
            downCircleAngle: 0.85 * Math.PI,
            ctxDown: {},
            downloadRatTop: 99.3,
            uploadRatTop: 79,
            duration: 600,
            ourTopSpeed: 0.7,
            liveRat: 0,
            liveRatBuf: 0,
            downLoadRat: 0,
            upLoadRat: 0,
            isUpLoad: false,
            testProgress: 0,
            isPc: this.isPc(),
            isInitTest: false,
            userClick: false,
            intervalID: 0,
            preloadList: [
                `https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/speed-test-down-done.svg`,
                `https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/speed-test-up-done.svg`,
            ]
        };
    },
    computed: {
        speedRatBuf() {
            return ((Math.min(this.speedRat, 70) * 300) / 100).toFixed(2);
        }
    },
    mounted() {
        // Preload pics
        this.preloadList.forEach(value => {
            let img = new Image()
            img.src = value
        })
        this.initCanvas();
        let that = this
        window.addEventListener('scroll', function () { //todo need remove this listener after init
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            let element = document.getElementsByClassName(that.isPc?'three-feature-container':'three-feature-container-mb')[0]
            let speedTestEl = document.getElementById('speed-test-el')
            // console.log(navigator.userAgent)
            // console.log(scrollTop + document.body.clientHeight)
            // console.log(element.offsetTop + speedTestEl.offsetHeight*(that.isPc?1: 1.3) + speedTestEl.offsetTop)
            // console.log(speedTestEl.offsetTop + speedTestEl.offsetHeight)
            if (navigator.userAgent.indexOf('Chrome') === -1 && navigator.userAgent.indexOf('Firefox') === -1) {
                if ((scrollTop + document.body.clientHeight) > speedTestEl.offsetTop + speedTestEl.offsetHeight && !that.isInitTest) {
                    that.isInitTest = true
                    let tl = new TimelineMax({});
                    let el = document.getElementById('speed-test-btn')
                    tl.to(el, 0.1, {
                            background: '#223F9D', transform: 'scale(0.9)'
                        }
                    ).to (el, 0.1, {
                        background: '#3963EF', transform: 'scale(1)'
                    }).call(that.testSpeed).call(function () {
                        setTimeout(function () {
                            that.intervalID = setInterval(function () {
                                if (!that.userClick && (that.testProgress === 0 || that.testProgress === 3)) {
                                    //clearInterval(that.intervalID)
                                    that.clickTip()
                                }
                            }, 2500)
                        }, 8000)
                    })
                }
            } else {
                if (element && (scrollTop + document.body.clientHeight) > element.offsetTop + speedTestEl.offsetHeight*(that.isPc?1: 1.3) + speedTestEl.offsetTop && !that.isInitTest) {
                    that.isInitTest = true
                    let tl = new TimelineMax({});
                    let el = document.getElementById('speed-test-btn')
                    tl.to(el, 0.1, {
                            background: '#223F9D', transform: 'scale(0.9)'
                        }
                    ).to (el, 0.1, {
                        background: '#3963EF', transform: 'scale(1)'
                    }).call(that.testSpeed).call(function () {
                        setTimeout(function () {
                            that.intervalID = setInterval(function () {
                                if (!that.userClick && (that.testProgress === 0 || that.testProgress === 3)) {
                                    //clearInterval(that.intervalID)
                                    that.clickTip()
                                }
                            }, 2500)
                        }, 8000)
                    })
                }
            }
        });

        // This handler will be executed every time the cursor
        // is moved over a different list item
        document.getElementById('speed-test-btn').addEventListener("mouseover", function(  ) {
            //todo need remove this listener
            that.userClick = true
        }, false);
        document.getElementById('speed-test-btn').addEventListener("mouseleave", function(  ) {
            //todo need remove this listener
            that.userClick = false
        }, false);
    },
    methods: {
        initCanvas() {
            let canvasBg = document.getElementById('canvas-bg');
            let ctxBg = canvasBg.getContext('2d');
            ctxBg.strokeStyle = '#28354E';
            ctxBg.lineWidth = 20;
            ctxBg.arc(148, 148, 128, 0.75 * Math.PI, 2.25 * Math.PI);
            ctxBg.stroke();

            if (!this.isPc) {
                this.resizeCanvas();
                window.addEventListener('resize', this.resizeCanvas, false);
            }

            let canvasDown = document.getElementById('canvas-down');
            this.ctxDown = canvasDown.getContext('2d');

        },
        userClickCall() {
            this.userClick = true
            this.testSpeed()
        },
        testSpeed() {
            if (this.testProgress === 0 || this.testProgress === 3) {
                this.testProgress = 1;
                this.downTest();
            }
        },
        downTest() {
            this.ctxDown.clearRect(0, 0, document.getElementById('canvas-down').width, document.getElementById('canvas-down').height);
            this.speedRat = this.speedRat + 3;
            let random = Math.random()*2;
            this.liveRat = Math.min(this.speedRat, this.isUpLoad ? this.uploadRatTop : this.downloadRatTop) + random;
            // If the liveRat bigger than 75, need to re-calculate, case this is nonlinear 
            this.reCalculateRat();
            // To distinguish between download or upload
            if (this.isUpLoad) {
                this.upLoadRat = this.liveRatBuf;
            } else {
                this.downLoadRat = this.liveRatBuf;
            }
            // calculate the arrows angle
            this.arrAngle = `rotate(${((((this.liveRat / 100) * 1.5) - 0.75) / 2) * 360 + 2}deg)`;
            // calculate the download circle angle
            this.downCircleAngle = (0.75 + (this.liveRat / 100) * 1.5) * Math.PI;
            this.speedCircle(this.downCircleAngle);
            if (this.speedRat <= this.duration) {
                requestAnimationFrame(this.downTest);
            } else {
                this.speedRat = this.isUpLoad ? this.uploadRatTop : this.downloadRatTop;
                this.liveRat = this.isUpLoad ? this.uploadRatTop : this.downloadRatTop;
                this.reCalculateRat();
                this.downTestDone();
            }
        },
        downTestDone() {
            this.ctxDown.clearRect(0, 0, document.getElementById('canvas-down').width, document.getElementById('canvas-down').height);
            this.speedRat = Math.max(this.speedRat - 6, 0);
            this.liveRat = this.speedRat;
            // If the liveRat bigger than 75, need to re-calculate, case this is nonlinear 
            this.reCalculateRat();
            // calculate the arrows angle
            this.arrAngle = `rotate(${((((this.liveRat / 100) * 1.5) - 0.75) / 2) * 360}deg)`;
            // calculate the download circle angle
            this.downCircleAngle = (0.75 + (this.liveRat / 100) * 1.5) * Math.PI;
            this.speedCircle(this.downCircleAngle);
            if (this.speedRat > 0) {
                requestAnimationFrame(this.downTestDone);
            } else {
                // If the upload test is done, then will stop
                if (!this.isUpLoad) {
                    this.isUpLoad = true;
                    this.testProgress = 2;
                    setTimeout(this.downTest, 1000);
                } else {
                    this.testProgress = 3;
                    this.isUpLoad = false;
                    this.userClick = false
                }
            }
        },
        reCalculateRat() {
            // If the liveRat bigger than 75, need to re-calculate, case this is nonlinear 
            if (this.liveRat > 75 && this.liveRat < 87.5) {
                this.liveRatBuf = 75 + (this.liveRat - 75) * 2.8;
            } else if (this.liveRat >= 87.5) {
                this.liveRatBuf = 150 + (this.liveRat - 75 - 12.5) * 12;
            } else {
                this.liveRatBuf = this.liveRat;
            }
        },
        speedCircle(n) {
            let gradient = this.ctxDown.createLinearGradient(0, 0, 296, 0);
            let startColor = this.isUpLoad ? '#3962EF' : '#3963EF';
            let endColor = this.isUpLoad ? '#734CEA' : '#2CC6FE';
            gradient.addColorStop(0, startColor);
            gradient.addColorStop(1, endColor);
            this.ctxDown.strokeStyle = gradient;
            this.ctxDown.shadowColor = startColor;
            if (this.isPc) {
                this.ctxDown.shadowBlur = 10;
                this.ctxDown.lineWidth = 20;
                this.ctxDown.save();
                this.ctxDown.beginPath();
                this.ctxDown.arc(148, 148, 128, 0.75 * Math.PI, n);
            } else {
                this.ctxDown.shadowBlur = window.innerWidth * 0.045 / 2;
                this.ctxDown.lineWidth = window.innerWidth * 0.045;
                this.ctxDown.save();
                this.ctxDown.beginPath();
                this.ctxDown.arc(window.innerWidth * 0.673 / 2, window.innerWidth * 0.673 / 2, (window.innerWidth * 0.63) / 2 - window.innerWidth * 0.045, 0.75 * Math.PI, n);
            }
            this.ctxDown.stroke();
            this.ctxDown.restore();
        },
        resizeCanvas() {
            let canvasBg = document.getElementById('canvas-bg');
            let canvasDown = document.getElementById('canvas-down');
            let ctxBg = canvasBg.getContext('2d');
            canvasBg.width = window.innerWidth * 0.673;
            canvasBg.height = window.innerWidth * 0.673;
            canvasDown.width = window.innerWidth * 0.673;
            canvasDown.height = window.innerWidth * 0.673;
            ctxBg.strokeStyle = '#28354E';
            ctxBg.lineWidth = window.innerWidth * 0.045;
            ctxBg.arc(window.innerWidth * 0.673 / 2, window.innerWidth * 0.673 / 2, (window.innerWidth * 0.63) / 2 - window.innerWidth * 0.045, 0.75 * Math.PI, 2.25 * Math.PI);
            ctxBg.stroke();
        },
        clickTip() {
            let tl = new TimelineMax({});
            let el = document.getElementById('speed-test-btn')
            tl.to(el, 0.15, {
                    background: '#3963EF', transform: 'scale(0.95)'
                }
            ).to (el, 0.15, {
                background: '#3963EF', transform: 'scale(1)'
            }).to (el, 0.1, {
                background: '#3963EF', transform: 'scale(1)'
            }).to(el, 0.15, {
                    background: '#3963EF', transform: 'scale(0.95)'
                    // background: '#3257D2', transform: 'scale(0.97)'
                }
            ).to (el, 0.15, {
                background: '#3963EF', transform: 'scale(1)'
            }).call(function () {
                this.userClick = false
            })
        },
        userFocus() {
            this.userClick = true
        }
    }
};
</script>

