<template>
    <div class="home-item" :class="{'protocols-tech-container': isPc, 'protocols-tech-container-mb': !isPc}">
        <div class="content">
            <p class="title">{{$t('home:screen10.Title-line-1')}}<br v-if="!isPc">{{$t('home:screen10.Title-line-2')}}</p>
            <p class="description">{{$t('home:screen10.Subtitle-line-1')}}<br v-if="!isPc">{{$t('home:screen10.Subtitle-line-2')}}</p>
            <div v-if="isPc" class="protocols-content">
                <div class="protocols-list">
                    <p @click="selectedProtocol(item, index)" :class="{'selected-protocol': item.isSelected}"
                       v-for="(item, index) in techList" :key="index">{{$t(item.title)}}</p>
                </div>
                <div class="description-list">
                    <p class="protocols-detail" v-for="(item,index) in selectedDescription" :key="index">{{$t(item)}}</p>
                </div>
            </div>
            <div v-if="!isPc" class="protocols-content">
                <div class="protocols-list" v-for="(item, index) in techList" :key="index">
                    <p class="protocols-title" @click="selectedProtocol(item, index)"
                       :class="{'selected-protocol': item.isSelected}"
                    >{{$t(item.title)}}</p>
                    <div class="description-list" v-if="item.isSelected">
                        <p class="protocols-detail" v-for="(descr,index) in item.description" :key="index">{{$t(descr)}}</p>
                    </div>
                </div>
                <!--<div class="description-list">-->
                <!--<p class="protocols-detail" v-for="item in selectedDescription">{{item}}</p>-->
                <!--</div>-->
            </div>
            <a-button v-if="isPc" class="use-btn" type="primary" @click="gotoOrder()">{{$t('home:screen10.Button')}}</a-button>
        </div>
    </div>
</template>

<script>

export default {
    name: 'HomeProtocolsTech',
    data() {
        return {
            techList: [
                {
                    title: 'home:screen10.Tab-1',
                    description: ['home:screen10.Paragraph-line-1',
                        'home:screen10.Paragraph-line-2'],
                    isSelected: true
                },
                {
                    title:  'home:screen10.Tab-2',
                    description: ['home:screen10.Paragraph-2'],
                    isSelected: false
                },
                {
                    title: 'home:screen10.Tab-3',
                    description: ['home:screen10.Paragraph-3'],
                    isSelected: false
                }
            ],
            selectedDescription: ['home:screen10.Paragraph-line-1', 'home:screen10.Paragraph-line-2'],
            isPc: this.isPc(),
            selectedIndex: 0
        };
    },
    methods: {
        selectedProtocol(item, selectedIndex) {
            this.techList.forEach((item, index) => {
                item.isSelected = (index === selectedIndex);
            });
            this.selectedDescription = item.description;
        },
        gotoOrder() {
            // gtag('event', 'try_now_multiProtocol')
            window.open(`${VUE_SITE}/order/place`,'_blank');
        }
    }
};
</script>

