<script>
import {AU_HMAC, AU_UUID} from "@/store/Global";

export default {
    name: 'PublicFooter',
    data() {
        return {
            isPc: this.isPc(),
            site_name: SITE_NAME,
        }
    },
    methods: {
        goToContactUs() {
            window.open(`${VUE_SITE}/help/contact-us`,'_self');
        },
        goToMyService() {
            if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
                this.$router.push({
                    path: `/user/service`,
                    name: 'service'
                });
            } else {
                this.$router.push({
                    path: 'user/login',
                    name: 'login'
                });
            }
        },
    }
};
</script>

