               v-if="item.name === 'netflix'"/>
          <img class="move-icon" src="https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/icon-telegram.svg"
               v-if="item.name === 'telegram'"/>
          <img class="move-icon" src="https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/icon-youtube.svg"
               v-if="item.name === 'youtube'"/>
