<template>
    <div class="home-item" :class="{'operational-data-container': isPc, 'operational-data-container-mb': !isPc}">
        <div class="content operational">
            <p class="title">{{$t('home:screen9.Title')}}</p>
            <p class="description">{{$t('home:screen9.Subtitle-line-1')}}<br v-if="!isPc">{{$t('home:screen9.Subtitle-line-2')}}</p>
            <div class="data-list">
                <div class="data-item">
                    <p class="data-title">{{speedTest.toFixed(0)}} Mbps</p>
                    <p class="data-detail">{{$t('home:screen9.Paragraph-1')}}</p>
                </div>
                <div class="data-item">
                    <p class="data-title">{{bandwidth.toFixed(0)}} Gbps</p>
                    <p class="data-detail">{{$t('home:screen9.Paragraph-2')}}</p>
                </div>
                <div class="data-item">
                    <p class="data-title">{{flow.toFixed(0)}} TB</p>
                    <p class="data-detail">{{$t('home:screen9.Paragraph-3')}}</p>
                </div>
                <div class="data-item">
                    <p class="data-title">{{useRatio.toFixed(2)}} %</p>
                    <p class="data-detail">{{$t('home:screen9.Paragraph-4')}}</p>
                </div>
                <div class="data-item">
                    <p class="data-title">{{numberOfUsers.toFixed(0)}}</p>
                    <p class="data-detail">{{$t('home:screen9.Paragraph-5')}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {TweenLite} from 'gsap';
export default {
    name: 'HomeOperationalData',
    data() {
        return {
            speedTest: 0,
            bandwidth: 0,
            flow: 0,
            useRatio: 0,
            numberOfUsers: 0,
            isPc: this.isPc(),
            animationDelay: 0.5
        }
    },
    props: {
        isShow: {}
    },
    watch: {
        isShow: {
            immediate: true,    // 
            handler(val) {
                if (val) {
                    TweenLite.to(this.$data, 3, {
                        speedTest: 960,
                        delay: this.animationDelay
                    });
                    TweenLite.to(this.$data, 3, {
                        bandwidth: 69,
                        delay: this.animationDelay*2
                    });
                    TweenLite.to(this.$data, 3, {
                        flow: 125,
                        delay: this.animationDelay*3
                    });
                    TweenLite.to(this.$data, 3, {
                        useRatio: 99.98,
                        delay: this.animationDelay*4
                    });
                    TweenLite.to(this.$data, 3, {
                        numberOfUsers: 30000,
                        delay: this.animationDelay*5
                    });
                }
            }
        }
    },

    mounted() {

    }
};
</script>

<style lang="scss">
.operational-data-container {
    padding: 130px 0;
    background: white;
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        .title {
            font-size: 40px;
            font-weight: 600;
            margin-bottom: 20px;
        }
        .description {
            font-size: 21px;
            font-weight: 400;
            color: #616577;
            margin-bottom: 80px;
        }
        .data-list {
            display: flex;
            flex-direction: row;
            .data-item {
                margin: 0 40px;
                .data-title {
                    width: 155px;
                    font-size: 32px;
                    font-weight: 400;
                    color: #3963EF;
                    text-align: left;
                    margin-bottom: 10px;
                }
                .data-detail {
                    font-size: 17px;
                    font-weight: 400;
                    color: #9DA3B9;
                    text-align: left;
                    margin-bottom: 0;
                    width: max-content;
                }
            }
        }
    }
}

.operational-data-container-mb {
    background: white;
    padding: 2rem 0.533rem;
    .content {
        display: flex;
        flex-direction: column;
        .title {
            font-size: 0.747rem;
            line-height: 1.013rem;
            font-weight: 600;
            margin-bottom: 0.48rem;
        }
        .description {
            font-size: 0.48rem;
            line-height: 0.827rem;
            font-weight: 400;
            color: #616577;
            margin-bottom: 1.333rem;
        }
        .data-list {
            display: flex;
            flex-wrap: wrap;
            .data-item {
                width: 4.467rem;
                margin-bottom: 1.28rem;
                .data-title {
                    width: 100%;
                    font-size: 0.533rem;
                    font-weight: 600;
                    color: #242C45;
                    text-align: left;
                    margin-bottom: 0.213rem;
                }
                .data-detail {
                    font-size: 0.373rem;
                    font-weight: 400;
                    color: #606D83;
                    text-align: left;
                    margin-bottom: 0;
                    width: 100%;
                }
                &:nth-of-type(5) {
                    margin-bottom: 0;
                }
            }
        }
    }
}
</style>
